/* Amr */
#overlay {
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
  cursor: pointer;
}

#AH_Sidenav {
  background-color: white;
  position: absolute;
  width: 30%;
  top: 68px;
  left: -40%;
  z-index: 9999;
  transition: left 1s;
  overflow-x: hidden;
  overflow-y: auto;
  font-size: 13px;
  padding-bottom: 5px;
  bottom: 0;
}

.AH_icon-img {
  width: 20px;
}

#AA_sideNavCol #AA_sideNavTop {
  border-right: 2px solid #c2c0c0;
  font-size: 12px;
}
#AA_sideNavCol #AA_sideNavTop li {
  margin-left: 3%;
}
#AA_sideNavCol #AA_sideNavTop li:hover {
  color: orangered;
  cursor: pointer;
}
#AA_sideNavCol #AA_sideNavTop .AA_departments {
  font-size: calc(0.63rem + 0.1vw);
  font-weight: bold;
  margin-left: 1%;
}
#AA_sideNavCol #AA_sideNavBottom {
  font-size: 13px;
}
#AA_sideNavCol #AA_sideNavBottom li {
  margin-left: 3%;
  margin-bottom: 1%;
}
#AA_sideNavCol #AA_sideNavBottom li:hover {
  color: orangered;
  cursor: pointer;
}
#AA_sideNavCol #AA_sideNavBottom .AA_department {
  font-size: calc(0.7rem + 0.09vw);
  font-weight: bold;
  margin-left: 1%;
}
#AA_sideNavCol #AA_sideNavBottom #AA_explore {
  font-size: calc(0.7rem + 0.1vw);
  font-weight: bold;
  margin-left: 3%;
}
#AA_sideNavCol #AA_checkBoxList {
  font-size: calc(0.7rem + 0.1vw);
}
