  font-size: calc(0.65rem + 0.1vw);
  font-weight: bold;
  border: none;
}
#AA_sideNavCol #AA_checkBoxList #AA_checkIcon {
  color: #f5b812;
  font-size: calc(1rem + 0.1vw);
}
