/* Serety */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.a-button {
  background: #e7e9ec;
  border-radius: 3px;
  border-color: #adb1b8 #a2a6ac #8d9096;
  border-style: solid;
  border-width: 1px;
  cursor: pointer;
  text-decoration: none !important;
  vertical-align: middle;
  font-size: 13px;
  line-height: 29px;
  padding: 0 10px 0 11px;
  text-align: center;
