  border-color: #a88734 #9c7e31 #846a29;
}
.a-button a {
  color: black;
}

#navBackToTop {
  background-color: #37475a !important;
  opacity: 0.8;
}
#navBackToTop:hover {
  background-color: #37475a !important;
  opacity: 1;
}
#navBackToTop a {
  text-decoration: none;
}

.upfooter {
  background-color: #232f3e;
}
.upfooter div ul li a {
  color: #ddd !important;
  font-size: 14px;
  text-decoration: none;
}
.upfooter .icp-button {
  width: 200px;
  height: 35px;
  color: #ddd;
  display: inline-block !important;
  border: 1px solid #848688;
  border-radius: 3px;
  padding: 6px 18px 6px 8px;
  font-size: 13px;
  margin: 4px;
  text-decoration: none;
  text-align: left;
}

.navFooter {
  background-color: #131a22;
}
.navFooter #grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(130px, 1fr));
  grid-template-rows: auto auto auto auto auto;
  gap: 1em 1em;
}
.navFooter a {
  font-size: 11px !important;
  line-height: 100%;
  color: white !important;
  text-decoration: none;
}
.navFooter a:hover {
  text-decoration: underline;
}
.navFooter a Span {
  color: #999 !important;
  font-size: 10px;
  line-height: 8px;
  padding: 0px;
}

#copyrights li {
  display: inline-block;
  padding: 0px 10px;
  font-size: 12px;
}

.splide__arrow--next {
  right: 0 !important;
}

.splide__arrow--prev {
  left: 0 !important;
}

.splide__slide {
  width: 200px;
  height: 330px !important;
  cursor: grab;
}
.splide__slide .product-image {
  width: auto;
  height: auto;
  max-width: 200px;
  max-height: 330px;
  vertical-align: middle;
}
.splide__slide:active {
  cursor: grabbing;
}

.splide__arrow {
  margin-top: 80px;
  width: 3em !important;
  height: 4em !important;
  border-radius: 0 !important;
  transform: translateY(8%);
}
.splide__arrow svg {
  width: 2.5em !important;
  height: 2.5em !important;
}

.splide__pagination {
  display: none !important;
}

.splide__track {
  overflow: auto !important;
}

.splide__track2 {
  overflow: hidden !important;
}

